import {
  Node,
  Block,
} from '@ali/4ever-cangjie';
import Table from '../mo/models';
import TableCell from '../mo/models/tableCell';

export function isTableCell(maybeTableCell: Node): maybeTableCell is Table {
  return TableCell.isTableCell(maybeTableCell);
}

export function isTableRow(maybeTableRow: Node): maybeTableRow is Table {
  return Block.isBlock(maybeTableRow) && maybeTableRow.type === 'table-row';
}

export function isTable(maybeTable: Node): maybeTable is Table {
  return Block.isBlock(maybeTable) && maybeTable.type === 'table';
}

export function isTableNode(maybeTable: Node): maybeTable is Block {
  return (
    isTableCell(maybeTable) || isTableRow(maybeTable) || isTable(maybeTable)
  );
}

export interface ITableSelection {
  key: string;
  endColIndex: number;
  endRowIndex: number;
  startColIndex: number;
  startRowIndex: number;
  // 选中行 还是 列
  type?: 'row' | 'col';
}

export interface ISelectedIndexRange {
  start: number;
  end: number;
}

export interface TableConfig {
  enablePruning?: boolean;
  hideDeleteButton?: boolean | (() => boolean);
  hideWidthFitButton?: boolean;
  isMobile?: boolean;
  locale?: Record<string, any>;
  disableHorizontalScrollbar?: boolean; // 是否禁用吸底横向滚动条
  onScroll?: (scrollContainer: HTMLDivElement | null, table: Table) => void;
  enableAutofitWidth?: () => boolean;
  disableDefaultAutofitWidth?: () => boolean;
  enableHeader?: () => boolean;
  setTableHeaderStyle?: () => void;
  enableHeaderSticky?: () => boolean;
  hideCornerButton?: () => boolean;
}

export type MobileTableConfig = {
  enableToolbar?: boolean;
} & TableConfig;

// TODO: local types
export type TableLocale = any;

export type VerticalAlign = 'middle' | 'top' | 'bottom';

declare module '@ali/4ever-cangjie' {
  /** 插件配置 */
  interface BiPluginConfig {
    table?: TableConfig;
  }
  interface MobileBiPluginConfig {
    table?: MobileTableConfig;
  }
}
